html {
  --static-color: #000;
  --active-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo-link {
  text-decoration: none;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__item:not(:last-of-type) {
  margin-right: 20px;
}

.nav__link {
  color: var(--static-color);
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover, .is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background: var(--active-color);
  border-radius: 4px;
  width: 100%;
  height: 5px;
  position: absolute;
  bottom: -1px;
  left: 0;
}
